.show-container {
  max-height: 120px;
  border: 1px solid #ddd;
  border-radius: 12px;
  padding: 20px;
  margin: 20px 15px;
  background-color: #fff;
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  display: flex;
  flex: 1;
  align-items: center;
  text-align: center;
  width: 90%;
  justify-content: start;
  gap: 40px;
  text-decoration: none;
}

.show-container .show-timings-image {
  width: 80px;
  border-radius: 5px;
}

.show-container li {
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  flex: 2;
  width: 120px;
  text-align: center;
  color: rgb(30, 180, 30);
  list-style-type: none;
}

.show-container h3 {
  width: 350px;
  text-align: justify;
}

.show-container .theatre-image {
  width: 120px;
  height: auto;
  margin-right: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  object-fit: cover;
}

.show-container .theatre-details {
  flex: 1;
}

.show-container .theatre-details h4 {
  margin: 8px 0;
  color: #333;
  font-weight: 400;
}

.show-container .theatre-details h4 strong {
  color: #666;
  font-weight: 600;
}

.show-container .show-timing {
  font-size: 14px;
  color: #555;
}

.show-container ul {
  margin-left: auto !important;
}
