@import 'variables.scss';
.dashboard-header {
  background-color:#1c1c1e;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  height: 80px;

  h4 {
    margin-left: 10px;
    margin-top: 12px;

  }
.header-right{
  display: flex;
}
  .status-button {
    padding: 10px;
    display: flex;
    gap: 7px;
    margin: 5px;
    background-color: $text-red;
    border: none;
    color: white;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background-color: darken(red, 10%);
    }
  }

  .header-logo {
    display: flex;

    img {
      height: 50px;
      width: auto;
    }
  }

  .header-search {
    flex: 1;
    display: flex;
    justify-content: center;

    .search-input {
      width: 100%;
      max-width: 500px;
      padding: 10px;
      border: none;
      border-radius: 5px;
      background-color: #333;
      color: #fff;
      font-size: 1rem;
      margin-top: 10px;

      &:focus {
        outline: none;
        background-color: #444;
      }
    }
  }
}
