@import 'variables.scss';

.booking-container {
  display: inline-block;
  position: relative;
  justify-content: space-between;
  padding: 10px;
  margin-bottom: 10px;
  flex-direction: row;
  justify-content: center;
  text-align: center;

  &.cancelled-booking {
    background-color: rgba(255, 0, 0, 0.1); 
    border: 1px solid #ff0000;  
  }
}
.bordercontainer{
  border-radius: 15px;
  border:1px solid rgb(223, 222, 222);
  width:170px;
}
a{
  color:black !important;
}
.cancelled-label {
  text-align: center;
  color: $legend-green; 

  h4{
    position: absolute;
    z-index: 100;
    transform: rotate(310deg);
    top: 98px;
    left: -1px;
    color: white;
    letter-spacing: 1px;
    font-weight: bold;
    font-size: 1.5rem;
    filter: brightness(1);
    color: $text-red;
    letter-spacing: 7px;
  }
}
.finished-label {
  text-align: center;
  color: $legend-green; 

  h4{
    position: absolute;
    transform: rotate(310deg);
    z-index: 100;
    top: 91px;
    left: -12px;
    padding: 10px;
    color: green;
    letter-spacing: 12px;
    font-weight: bold;
    font-size: 1.5rem;
    filter: brightness(1);
  }
}
.upcoming-label {
  text-align: center;
  color: $legend-green; 

  h4{
    position: absolute;
    z-index: 100;
    left: 26px;
    color: white;
    letter-spacing: 1px;
    font-weight: bold;
    font-size: 1.5rem;
    filter: brightness(0.95);
  }
}

.booking-details-left {
  flex: 1;
  margin-right: 20px;
}

.booking-details-right {
  flex: 1;
  text-align: left;
  font-size:15px;
  width:170px;
  padding: 0px 5px;
  .timings{
    font-size:12px;
    color:#444;
  }
  .mov-name{
    text-transform: uppercase;
    font-weight: bold;
  }
}

.booking-details-left h3 {
  margin-bottom: 10px;
}

.booking-details-right p {
  margin-bottom: 5px;
}

.booking-poster {
  width: 170px;
  margin-bottom: 10px;
  border-radius: 5px;
  filter: brightness(0.6);
}

    


  .booking-details {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: $box-shadow;
    background-color: #fff;
    display: flex;
    gap:20px;
  
  
    .booking-movie-image {
      width:auto;
      display: block;
      max-width: 100%;
      height: 400px;
      border-radius: 8px;
    }
  
    h3 {
      font-size: 24px;
      margin-bottom: 10px;
      color: #333;
      font-weight: bold;
      text-transform: uppercase;
    }
    h2{
      font-size:18px;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom:10px;
    }
  
    p {
      font-size: 15px;
      margin-top:-10px;
      color: #666;
  
      strong {
        color: #333;
      }
    }
  
    h4 {
      font-size: 16px;
      color: #444;
    }

    h5{
      font-size: 18px;
      margin-bottom:10px;
      text-transform: uppercase;
      font-weight: bold;
      display: inline;
  }

  h6{
    padding:5px;
    gap: 10px;
  }

    }
  
    ul {
      padding-left: 20px;
      margin-top: 10px;
  
      li {
        font-size: 16px;
        color: #333;
        margin-bottom: 5px;
      }
    }
    .cancelled-booking{
      background-color: $text-red;
    }
  
  
  .option-button{
    padding: 5px; 
    margin-top: 5px;
    width:170px;
    background-color: $text-red; 
    padding:10px;
    border: none; 
    color: $white;
    font-size: 16px; 
    border-radius: 5px;
    cursor: pointer;

    &:hover {
        background-color: darken($text-red, 10%); 
    }    }

    .show-details{
      display: flex;
      gap: 30px;
    }

      table h4{
        font-size: 24px;
        margin-bottom: 10px;
        color: #333;
        font-weight: bold;
        text-transform: uppercase;
      }
      th{
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 7px;
      }
      td{
        font-size:15px;
        color:#444;
        font-weight: 600;
        margin-right:7px;
      }

      .status{
        position: relative;
        width: auto;
        text-align: center;
        max-width: 307px;
        z-index: 10001;
        background-color: #2bc72b;
        padding: 10px;
        font-size: 15px;
        color: white;
        font-weight: bold;
        border-radius: 5px;
        top: 14px;
        margin-bottom: 10px;
    }
    .booking-cancelled{
      background-color: $text-red;
      }