.theatre-container {
  border-radius: 8px;
  margin: 15px 10px;
  background-color: transparent;
  transition: background-color 0.3s;
  display: inline-block;
  align-items: center;
  justify-content: left;

  .theatre {
    border-radius: 10px;
    width: 237px;
    border: 1px solid #dcdcdb;
  }

  img {
    width: 100%;
    text-align: center;
    height: 256px;
    margin-right: 15px;
    border-radius: 4px;
  }

  button {
    padding: 10px 0px;
    display: block;
    width: 100%;
    background-color: $text-red;
    border: none;
    color: white;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background-color: #9a1010;
      color: #fff;
    }
  }

  .theatre-details {
    flex: 1;

    h4 {
      padding: 10px;
      text-align: center;
      margin: 6px 0;
      color: #000000;
      font-weight: 500;
      font-size: 18px;

      span {
        color: #f34444;
      }
    }
  }
}

.theatre-info {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;

  .theatre-image {
    width: 150px;
    height: 225px;
    object-fit: cover;
    border-radius: 10px;
    margin-right: 20px;
    border: 1px solid #ffffff;
  }

  h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }

  h4 {
    font-size: 18px;
    margin-bottom: 5px;
    color: #666;
  }

  h4:last-child {
    margin-bottom: 0;
  }
}
