@import 'variables.scss';

:root {
  --legend-red: red;
  --legend-green: rgb(31, 186, 31);
  --legend-white: white;
  --black: black;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", serif;
  font-weight: 500;
  font-style: normal;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.screen {
  width: 100%;
  width: 529px;
  height: 30px;
  background-color: rgb(90, 86, 86);
  border-radius: 10px;
  text-align: center;
  line-height: 30px;
  color: white;
  margin-bottom: 20px;
  box-shadow: inset;
}

.seating-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;
}

.seat-row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
  gap: 3px;
}

.seat {
  width: 40px;
  height: 40px;
  margin: 5px;
  padding-top:4px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19) inset;
  border-top-left-radius: 23px;
  border-top-right-radius: 23px;
  font-weight: 600;
  font-size:12px;

  &.booked {
    background-color:rgb(247, 143, 143);
    cursor: not-allowed;
  }

  &.selected {
    background-color: var(--legend-red)!important;
    color:white;
  }

  &.vacant {
    background-color: var(--legend-white);
  }

  &:hover:not(.booked):not(.selected) {
    transform:scale(1.1);
  }
}

.legend {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 500px;
}

.legend-item {
  display: flex;
  align-items: center;
}

.legend-box {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-top-left-radius: 17px;
  border-top-right-radius: 17px;
  transition: background-color 0.3s;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19) inset;

  &.vacant-box {
    background-color: var(--legend-white);
  }

  &.selected-box {
    background-color: var(--legend-red);
    color:white;
  }

  &.booked-box {
    background-color: rgb(247, 143, 143);
    color:white;
  }
}

.legend-item p {
  margin: 0;
  font-size: 14px;
}

.book-button {
  margin-top: 30px;
  padding: 10px 0px;
  display: block;
  width: 200px;
  background-color: $text-red;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #9a1010;
    color: #fff;
  }
}
