@import 'variables.scss';

.h1, .signing-page {
  color: $text-red;
}

.title-container {
  position: absolute;
  top: 40px;
  text-align: center;
  width: 100%;

  .title {
    color: $text-red;
    font-family: 'Times New Roman', Times, serif; 
    font-size: 3.5rem;
    margin: 0;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; 
}

.signing-page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 400px; 
  position: relative;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  padding: 20px; 

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    opacity: 0.9;
    z-index: -1;
  }

  .flipper {
    width: 100%;
    height: 400px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.6s;
  }

  .login-section, .register-section { 
    width: 100%;
    height: 110%;
    padding: 30px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 20px;
    position: absolute;
    backface-visibility: hidden;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 0 15px $text-red;

    .close-button{
      background-color: $text-red;
      width:40px;
      border-radius: 5px;
      margin-left: 90%;
      border: 0;
    }
    h2 {
      text-align: center;
      color: $text-red;
      margin-bottom: 20px;
    }

    .form-group {
      margin-bottom: 15px;

      label {
        display: block;
        font-weight: bold;
        color: $text-red;
        margin-bottom: 5px;
      }

      input {
        width: 300px;
        padding: 10px;
        border: 1px solid $text-red;
        border-radius: 5px;
        background-color: $white;
        color: $black;

        &::placeholder {
          color: rgba(0, 0, 0, 0.5);
        }
      }
    }

    .login-btn, .register-btn {
      width: 100%;
      padding: 10px;
      background-color: $text-red;
      border: none;
      color: white;
      font-size: 16px;
      border-radius: 5px;
      cursor: pointer;

      &:hover {
        background-color: darken($text-red, 10%);
      }
    }

    .login-footer {
      margin-top: 20px;
      text-align: center;

      a {
        color: $text-red!important;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .login-section {
    z-index: 2;
    transform: rotateY(0deg);
  }

  .register-section {
    position:absolute;
    margin-left:-175px;
    justify-content: center;
    transform: rotateY(180deg);
    height: 525px;
    width:700px;
    z-index:10000;
    margin-top: -80px;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .register-section form{
    display:flex;
    flex-direction: row;
    justify-content: center;
    gap:20px;
  }

  .flip {
    transform: rotateY(180deg);
  }
}
