
.movie-container {
  border-radius: 8px;
  margin: 15px 10px;
  background-color: transparent;
  transition: background-color 0.3s;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: left;
  width: auto;
  .movie-image {
    width: 100%; 
    height: 300px;
    text-align: center; 
    margin-right: 15px;
    border-radius: 4px; 
  }
  button{
    padding: 10px 0px;
    display:block;
    width:100%;
    background-color: $text-red;
    border: none;
    color: white;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background-color: #9a1010;
      color: #fff; 
    }
  }
  .movie{
    border-radius:10px;
    width:250px;
    border:1px solid rgb(220, 220, 219);
    &:hover{
      transform: scale(1.1);
    }
  }

  .fils {

    h4 {
      padding: 5px;
      margin: 6px 0;
      color: #000000;
      font-weight: bold;
      text-decoration: none;
      font-size: 18px;
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    p{
      padding: 0px 3px;
      color: #6b6b69;
      font-size: 11px;
      text-transform: capitalize;
      margin-top: -10px;
      margin-left: 2px;
    }
  }

}

.sub-header{
  display:flex;
  gap:10px;
  justify-content: space-between;
}
.movie-details {

  h4 {
    margin: 6px 0;
    color: #000000;
    font-weight: bold;
    text-decoration: none;
    font-size: 18px;
    margin-bottom: 10px;
    text-transform: uppercase;
    padding-left: 6px;
  }
  p{
    padding: 0px 3px;
    color: #6b6b69;
    font-size: 11px;
    text-transform: capitalize;
    margin-top: -10px;
    margin-left: 2px;
  }
}
.movie-info {
  max-height: 127px;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  flex:2;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
  margin-left: 10px;


  .movie-image {
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
    margin-right: 20px;
    border: 1px solid #ffffff;
  }

  h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }

  h4 {
    font-size: 13px;
    margin-bottom: 5px;
    color: #666;
  }

  h4:last-child {
    margin-bottom: 0; 
  }
}

.citypopupoverlay{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  padding-top:100px;
  .city-cont{
    display: flex;
    flex-direction: column;
    text-align: center;
    border:1px solid rgb(220, 220, 219);
    padding:10px;
    font-size: 12px;

    h4{
      font-size:12px;
    }
    
    img{
      width:50px;
    }

    
  }
  .city-outer{
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 800px;

    h1{
      font-size:18px;
      padding-top: 20px;
      padding-left:20px;
      font-weight: 600;

    }
  }
  .city-inner{
    display: flex;
    justify-content: center;
    gap:20px;
    padding: 20px;
  }
  
}
.image-preview {
  margin-top: 10px;
}

.preview-image {
  width: 150px;
  height: auto;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  background: #f4f4f4;
  margin-bottom: 10px;

}