.dashboard-sidebar {
  background-color: #1c1c1e;
  color: #fff;
  width: 250px;
  height: calc(100vh - 80px);
  position: fixed;
  top: 80px;
  left: 0;
  padding: 10px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  z-index: 1000;

  ul {
    list-style: none;
    gap: 30px;
    padding: 0;
    margin-top: 15%;

    li {
      padding: 15px 20px;
      margin-bottom: 10px;
      display: flex;
      gap: 10px;
      cursor: pointer;
      font-size: 1rem;
      font-weight: 500;
      color: #fff;
      border-radius: 5px;
      transition: all 0.3s ease;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(45deg, #c50812, #ff6b6b);
        opacity: 0;
        transition: opacity 0.3s ease;
        border-radius: 5px;
        z-index: -1;
      }

      &:hover::before {
        opacity: 0.9;
      }

      &:hover {
        color: #fff;
      }

      &.active {
        background-color: #c50812;
      }
    }
  }
}
