@import 'variables.scss';
.date-container {
  margin-left: 20px;
  display: flex;
  overflow-x: auto; 
  white-space: nowrap; 
  width: 90%;
  margin-bottom: 20px;

  .date-box {
    padding: 8px;
    border-radius: 5px;
    height: 60px;
    margin: 3px;
    align-content: center;
    text-align: center;
    border: 1px solid lightgray;

    .date-btn {
      border: none;
      margin-bottom: 20px;
      white-space: pre-wrap;
      background: $white;
      width: 33px;
      color: $text-red;
    }
  }

  .selected-date {
    background-color: $text-red;
    .date-btn {
      background-color: $text-red;
      color: white;
    }
  }
}
